.file {
  width: 100%;
  padding-top: 0.3rem;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  .file_top {
    width: 80%;
    height: 1.8rem;
    box-shadow: 0 0 0.2rem 0.1rem rgba(105, 106, 230, 0.05);
    background: #4ab58e;
    border-radius: 0.12rem;
    position: relative;
    padding: 0.5rem 0 0.6rem 0.6rem;
    .bg_right {
      width: 24%;
      height: 100%;
      background: url(../../../assets/images/bg_file.png);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      position: absolute;
      right: 0;
      bottom: 0px;
    }
    .file_toptext {
      .file_leftname {
        color: #ffffff;
        .file_title {
          font-size: 0.22rem;
          font-weight: 400;
        }
        .file_subtitle {
          font-size: 0.14rem;
        }
      }
      .file_searh {
        background-color: #fff;
        border-radius: 0.12rem;
        height: 0.5rem;
        line-height: 50px;
        width: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding-left: 0.2rem;
        .file_searhbtn {
          position: absolute;
          right: 0.15rem;
          background-color: #000;
          color: #fff;
          width: 0.6rem;
          height: 0.35rem;
          line-height: 0.35rem;
          text-align: center;
          border-radius: 0.05rem;
          cursor: pointer;
        }
      }
    }
  }
  .file_con {
    // min-height: 60%;
    width: 80%;
    // height: 500px;
    margin-top: 0.3rem;
    justify-content: space-between;
    position: relative;
    .left_dowm {
      border-radius: 0.12rem;
      // width: 65%;
      height: 100%;
      background-color: #fff;
      .file_page {
        margin-top: 0.2rem;
        margin-left: 0.1rem;
      }
    }
    .right_menu {
      border-radius: 0.12rem;
      // width: 30%;
      height: 100%;
      background-color: #fff;
      .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
        background-color: #fff;
      }
      .ant-menu-inline .ant-menu-item-selected::after {
        opacity: 0;
      }

      .menu_top {
        padding: 0.3rem 0 0.1rem 0.3rem;
        font-size: 0.16rem;
        font-weight: 500;
        color: #040e34;
      }
      .Menu {
        padding-left: 0.2rem;
        .menuitem {
          justify-content: space-between;
          align-items: center;
          .menuitemname {
            width: 80%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .filenum {
            padding: 0.03rem 0.1rem;
            border-radius: 0.05rem;
            height: 0.3rem;
            line-height: 0.26rem;
          }
        }
      }
    }
  }
}
.colornum1 {
  color: #1bc5bd;
  background-color: #c9f7f5;
}
.colornum2 {
  color: #ffa800;
  background-color: #fff4de;
}
