}
.downloadicon {
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #9696a3;
  background-color: #f0f0fd;
  border-radius: 0.05rem;
  text-align: center;
  font-size: 0.2rem;
  font-weight: bold;
}
.downloadicon:hover {
  color: #5c5ee4;
}
.options {
  display: flex;
  align-items: center;
  justify-content: center;
}

